import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { StartpageComponent } from './startpage/startpage.component';
import { CharpageComponent } from './charpage/charpage.component';
import { CharactersComponent } from './charpage/characters/characters.component';
import { ChardetailsComponent } from './charpage/chardetails/chardetails.component';

const routes: Routes = [
  { path: '', redirectTo: 'home', pathMatch: 'full' },
  { path: 'home', component: StartpageComponent },
  { path: 'characters', component: CharpageComponent, children: [
    { path: '',  redirectTo: 'charlist', pathMatch: 'full'},
    { path: 'charlist',  component: CharactersComponent},
    { path: 'charlist/:id',  component: ChardetailsComponent}
    
  ] }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
